.homePage * {
    box-sizing: border-box;
}

.homePage body {
    font-size: 16px;
    color: black;
    background-color: #f5f5f5;
}

.homePage #title {
    text-align: center;
    padding: 13vh;
    background-color: #f5f5f5;
}


.homePage #lead_text {
    color: black;
}


.homePage .display-4 {
    font-weight: bold;
    color: black;
}

.homePage .title_display-4 {
    color: black;
    font-weight: bold;
}


.homePage .Budget_Photos_Seating_Notes {
    text-align: center;
    margin-top: 2%;
}

@media screen and (min-width: 1200px) {

    .homePage #title {
        background-image: url('./img/open_img.png');
        background-size: auto;
        background-repeat: no-repeat;
        text-align: center;
        padding-top: 2vh;
        padding-bottom: 30vh;
        width: 100%;
    }

    .homePage .title_display-4 {
        color: whitesmoke;
        font-weight: bold;
    }

    .homePage .Budget_Photos_Seating_Notes {
        display: flex;
        text-align: center;
        justify-content: space-around;
        margin-top: 2%;
    }

    .homePage .Budget_Photos_Seating_Notes div {
        flex-basis: 22%;
        padding: 10vh;
        padding-bottom: 17vh;
    }

    .homePage #Budget:hover, #Photos:hover, #Seating:hover, #Notes:hover {
        transform: scale(1.03);
    }

    .homePage #Budget, #Photos, #Seating, #Notes {
        padding-top: 1vh;
    }

}

.homePage #Photos {
    background-image: url("./img/photos.png");
    background-size: cover;
}


.homePage #Seating {
    background-image: url("./img/seating.png");
    background-size: cover;
}

.homePage #Notes {
    background-image: url("./img/Notes.png");
    background-size: cover;
}

.homePage #Budget {
    background-image: url("./img/budget.png");
    background-size: cover;
}


@media screen and (max-width: 1199px) {

    .homePage .Budget_Photos_Seating_Notes div {
        height: 50vh;
    }

    .homePage .Budget_Photos_Seating_Notes {
        padding-left: 1vh;
        padding-right: 1vh;
    }

}
